.card {
    width: 250px;
    border-radius: 10px;
    margin:0.5em;
}

.card-img-top {
    border-top-right-radius: 10px;
    border-top-left-radius: 10px
}

span.text-muted {
    font-size: 15px
}

small.text-muted {
    font-size: 10px
}

h5.mb-0 {
    font-size: 1rem
}

small.ghj {
    font-size: 11px
}

.mid {
    background: #ECEDF1
}

h6.ml-1 {
    font-size: 15px
}

small.key {
    text-decoration: underline;
    font-size: 11px;
    cursor: pointer
}

.btn-danger {
    color: #FFCBD2
}

.btn-danger:focus {
    box-shadow: none;
}

small.justify-content-center {
    font-size: 11px;
    cursor: pointer;
    text-decoration: underline
}